<template>
  <div class="big_box">
    <!-- 住多久 -->
    <section class="times">
      <p class="howlong">住多久</p>
      <div class="choose_time">
        <div class="left">
          <div>
            {{ now_time }}
            <br />
            <span>现在入住</span>
          </div>
          <img
            src="@/assets/right.png"
            class="img_right"
            alt
          />
          <div @click="leaveShow = true">
            {{ end_time }}{{ weedday }}
            <br />
            <span>{{leaveTime}}前离店</span>
          </div>
          <img
            src="@/assets/right.png"
            class="img_right"
            alt
          />
        </div>
        <div class="right">
          {{ dataForm.diffDay }}
          <span class="unit">晚</span>
        </div>
      </div>
    </section>
    <!-- 选择房间 -->
    <p class="title">选择房间</p>
    <div class="scroll_box">
      <!-- 房间 -->
      <section class="bottom_room">
        <div
          class="room_box"
          v-for="(scale,i) in roomsList"
          :key="i"
          id="colorBox"
          @click="chooseRoom(scale)"
        >
          <div class="child1">{{scale.roomSn}}</div>
        </div>
      </section>
    </div>
    <van-calendar
      v-model="leaveShow"
      :min-date='minDate'
      :max-date="maxDate"
      @confirm="onConfirm"
    />
  </div>
</template>
<script>
import { GetBusinessRoomFreeHandleByhotel } from '@/api/api_business';
import dayjs from 'dayjs';
export default {
  data() {
    return {
      now_time: '',
      end_time: null,
      dataForm: {
        diffDay: 1
      },
      time_box: false,
      roomsList: [],
      leaveTime: '',
      leaveShow: false,
      end_time: '',
      weedday: '',
      minDate: new Date(),
      maxDate: new Date()
    };
  },
  created() {
    this.$store.commit('setHeader', '散客入住');
    this.getRooms();
    this.weeks = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];
    let nowHours = new Date().getHours();
    if (0 <= nowHours && nowHours < 8) {
      this.lastDay = -1;
      this.now_time = this.GetDateStr(-1);
      this.end_time =
        (new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1) + '月' + (new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()) + '日' + ' ' + this.weeks[new Date().getDay()];
      this.maxDate = new Date(dayjs(this.minDate).add(3, 'year').format('YYYY-MM-DD'))
    } else {
      this.minDate = new Date(dayjs(this.minDate).add(1, 'day').format('YYYY-MM-DD'))
      this.maxDate = new Date(dayjs(this.minDate).add(1, 'day').add(3, 'year').format('YYYY-MM-DD'))
      this.now_time =
        (new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1) + '月' + (new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()) + '日' + ' ' + this.weeks[new Date().getDay()];
      this.end_time = this.GetDateStr(this.dataForm.diffDay);
    }
    this.c_hotel = JSON.parse(localStorage.getItem('c_hotel'));
  },
  methods: {
    // 根据酒店查询空闲房间及房态
    getRooms() {
      GetBusinessRoomFreeHandleByhotel({ hotelId: this.$route.query.hotelId, diffDay: this.dataForm.diffDay }).done((res) => {
        this.roomsList = res.order;
        this.leaveTime = res.data.leaveTime;
      });
    },
    // 获取几天后的时间
    GetDateStr(AddDayCount) {
      var dd = new Date();
      dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期
      var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; //获取当前月份的日期，不足10补0
      var d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate(); //获取当前几号，不足10补0
      var z = this.weeks[dd.getDay()]; //获取当前几号，不足10补0
      return m + '月' + d + '日' + ' ' + z;
    },
    // 选择房间
    chooseRoom(item) {
      this.$router.push({
        path: '/business-handle-choose-room',
        query: {
          hotelId: item.hotelId,
          roomId: item.id,
          roomScaleId: item.roomScaleId,
          roomSn: item.roomSn,
          now_time: this.now_time,
          end_time: this.dataForm.diffDay == 1 ? this.end_time.substr(0, 6) : this.new_end_time,
          diffDay: this.dataForm.diffDay
        }
      });
    },
    // 选择开始时间-1
    formatDate(date) {
      let y = date.getFullYear()
      let m = (date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1)
      let d = (date.getDate()) < 10 ? '0' + (date.getDate()) : (date.getDate())
      this.weedday = this.weeks[dayjs(date).day()]
      this.dataForm.diffDay = dayjs(date).diff(dayjs(), 'day') + 1
      this.getRooms();
      this.new_end_time = `${m}月${d}日`
      return `${m}月${d}日`;
    },
    onConfirm(date) {
      this.leaveShow = false;
      this.end_time = this.formatDate(date)
    }
  }
};
</script>
<style scoped>
.big_box {
    font-size: 0.6rem;
}
.times {
    text-align: left;
}
.title {
    font-size: 0.9rem;
    font-weight: bold;
    padding: 0.6rem 0.4rem;
    background: #f6f6f6;
    text-align: left;
}
.scroll_box {
    position: fixed;
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    flex-wrap: wrap;
    justify-content: center;
    background: #fff;
}
.howlong {
    font-size: 0.9rem;
    font-weight: bold;
    padding: 0.6rem 0.4rem;
    background: #f6f6f6;
}
.choose_time {
    background: #fff;
    font-size: 0.6rem;
    color: #313443;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.4rem 0.8rem 0.4rem 0.4rem;
}
.room_box {
    text-align: left;
    padding: 10px;
    box-sizing: border-box;
    width: 32%;
    height: 70px;
    background: #f0f0f0;
    margin: 2px;
}
.bottom_room {
    display: flex;
    width: 100%;
    text-align: center;
    flex-wrap: wrap;
    padding: 0.2rem 0.6rem 0.6rem 0.6rem;
    box-sizing: border-box;
    justify-content: flex-start;
}
.child1 {
    font-size: 0.9rem;
    font-weight: bold;
    color: rgba(49, 52, 67, 1);
    opacity: 1;
}
.left {
    color: rgba(49, 52, 67, 1);
    opacity: 0.53;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 58%;
}
.img_right {
    width: 1rem;
    height: 1rem;
}
.right {
    color: #ff7d41;
    font-weight: bold;
    font-size: 1.8rem;
}
.unit {
    font-weight: normal;
    font-size: 0.9rem;
}
.img_unit {
    width: 0.8rem;
    height: 0.8rem;
}
/* 弹出框的宽度 */
.showbox {
    width: 80%;
    border-radius: 5px;
    padding: 8px 10px;
    color: #313443;
    font-weight: bold;
    font-size: 0.9rem;
}
.show1 {
    line-height: 35px;
    background: #f7f7f7;
    margin: 4px 0;
    letter-spacing: 8px;
}
#colorBox:active {
    background: rgba(255, 125, 65, 1);
    opacity: 0.95;
    cursor: pointer;
}
</style>



